.productdisplay{
    display: flex;
    margin: 0px 170px;
}

.productdisplay-left{
    display: flex;
    gap:17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.productdisplay-img-list img{
    height:163px;
}

.productdisplay-main-img{
    width: 586px;
    height:700px;
    border-radius: 18px;
}

.productdisplay-right{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;
}
 .productdisplay-right h1{
    color:#3d3d3d;
    font-size:40px ;
    font-weight: 700;
 }

 .productdisplay-right-star{
    display: flex;
    align-items: center;
    margin-top:13px;
    font-size: 16px;
    gap: 5px;
    color:#1c1c1c;
 }

 .productdisplay-right-prices{
    gap: 30px;
    font-weight: 700;
    font-size: 24px;
    margin: 40px 0px;
    display: flex;
 }

 .productdisplay-right-price-old{
    color:#818181;
    text-decoration: line-through;
 }

 .productdisplay-right-price-new{
    color: #ff4141;
 }
 .productdisplay-right-size h1{
    margin-top: 55px;
    color:#656565;
    font-size: 20px;
    font-weight: 600;
 }

 .productdisplay-right-sizes{
    display: flex;
    margin:30px 0px;
    gap: 20px;
 }

 .productdisplay-right-sizes div{
    padding: 18px 24px;
    background-color: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
 }

 .productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color:white;
    background: #FF4141;
    margin-bottom: 40px;
    border: none;
    outline:none;
    cursor: pointer;
 }

 .productdisplay-right button:hover{
   background-color: #da1f1fa3;
 }

 

 .productdisplay-right-category{
    margin-top: 10px;
 }

 .productdisplay-right-category span{
    font-weight: 600;
 }

 .overlay {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black */
   z-index: 999; /* Ensure it's above other content */
 }
 
 .message-container {
   position: fixed;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   z-index: 1000; /* Ensure it's above the overlay */
   height:50px;
   width: 400px;
   justify-content: center;
   display: flex;
   align-items: center;
 }
 
 .message {
   background-color: #0c3724;
   padding: 10px;
   border-radius: 18px;
   font-size: 26px;
   font-weight: 500;
   color: #fff;
 }

 .productdisplay-right-sizes div.active{
   background-color:#ff4141;
 }

 @media(max-width:1280px){
   .prodcutdisplay{
      margin: 0px 60px;
   }
   .productdisplay-left{
      gap: 10px;
   }
   .productdisplay-img-list{
      gap: 10px;
   }
   .productdisplay-img-list img{
      height: 120px;
   }
   .productdisplay-main-img{
      width: auto;
      height: 510px;
   }
   .productdisplay-right{
      margin: 0px 30px;
   }
   .productdisplay-right h1{
      font-size: 22px;
   }
   .productdisplay-right-stars{
      gap: 3px;
      font-size: 13px;
   }
   .productdisplay-right-prices{
      margin: 10px 0px;
      font-size: 18px;
   }
   .productdisplay-right-description{
      font-size: 13px;
   }
   .productdisplay-right-size h1{
      margin-top: 20px;
      font-size: 20px;
   }
   .productdisplay-right-sizes div{
      padding: 14px 20px;
   }
   .productdisplay-right-button{
      width: 150px;
      padding: 15px 0px;
      margin-bottom: 20px;
   }
   .productdisplay-right-category{
      margin-top: 5px;
      font-size: 14px;
   }
 }

 @media(max-width:1024px){
