      font-size: 12px;
   }
 }
 @media(max-width:800px){
   .prodcutdisplay{
      margin: 0px 10px;
   }
   .productdisplay-left{
      gap: 0px;
      flex-direction: column;
      align-items: center;
      padding-top: 0;
   }
   .productdisplay-img-list{
      flex-direction: row;
      gap: 6px;
   }
 
   .productdisplay-img-list img{
      height: 70px;
   }
   .productdisplay-main-img{
      height: 300px;
   }
 
   .productdisplay-right h1{
      font-size: 14px;
   }
   .productdisplay-right-stars img{
      width: 13px;
   }
   .productdisplay-right-description{
      font-size: 10px;
   }
   .prodcutdisplay-right-size h1{
      margin-top: 10px;
   }
   .productdisplay-right-sizes{
      margin-top: 10px ;
