   }
 }
 @media(max-width:500px){
   .productdisplay{
      flex-direction: column;
      margin-left:10px;
   }
   .productdisplay-left{
      gap: 10px;
      flex-direction: row;
      margin: auto;
      padding-left: 20px;
   }
   .productdisplay-img-list{
      flex-direction: column;
      gap: 8px;
   }
   .productdisplay-img-list img{
      height: 75px;
   }
   .productdisplay-main-img{
      height: 330px;
   }
   .productdisplay-right{
      margin: 5px;
   }
   .prodcutdisplay-right-size h1{
      margin-top: 15px;
      font-size: 20px;
      font-weight: 500;
   }
   .productdisplay-right-stars img{
      width: 15px;
   }
   .productdisplay-right-sizes div{
      padding: 10px 16px;
      font-size: 14px;
   }
   .prodcutdisplay-right-sizes{
      gap: 10px;
      margin: 20px 0px;
   }
   .productdisplay-right-button{
      width: 130px;
      font-size: 15px;
      padding: 12px 0px;
   }
   .productdisplay-right-category{
      font-size: 16px;
   }
 }
 