.navbar{
    display:flex;
    justify-content:space-around;
    padding:16px;
    box-shadow:0 1px 3px -2px black;
    background-color: #221d1dd6;
    align-items: center;
}

.logo{
    display:flex;
    align-items:center;
    gap:10px;
}

.logo img{
    height:100px;
    width:200px;
}

.nav-menu{
    display:flex;
    align-items:center;
    list-style:none;
    gap:50px;
    color:#626262;
    font-size:20px;
    font-weight:500;
}

.nav-menu li{
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    gap:3px;
    cursor:pointer;
}
.nav-menu a{
    color:white;
}

.nav-menu hr{
    border:none;
    width:80%;
    height:3px;
    border-radius: 10px;
    background:#FF4141;
}

.nav-login-cart{
    display:flex;
    align-items: center;
    gap:45px;
}

.nav-login-cart button{
    width:157px;
    height:58px;
    outline:none;
    border: 1px solid #7a7a7a;
    border-radius:75px;
    color:#515151;
    font-size:20px;
    font-weight:500;
    background:white;
    cursor:pointer;
}

.nav-login-cart button:active{
    background-color: #f3f3f3;
}

.nav-cart-count{
    width:22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left:-55px;
    border-radius:11px;
    font-size: 14px;
    background: red;
    color:white;
}

.nav-dropdown{
    display:none;
}

@media(max-width:1280px){
    .navbar{
        padding:12px 50px;
    }
    .nav-logo-img{
        width: 40px;
    }
    .nav-logo p{
        font-size: 25px;
    }
    .nav-menu{
        gap: 30px;
        font-size: 16px;
    }
    .nav-login-cart{
        gap: 30px;
    }
    .nav-login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }
}

@media(max-width:1024px){
    .navbar{
        padding: 12px 30px;
    }
    .nav-menu{
        gap: 25px;
        font-size: 14px;
    }
    .nav-login-cart button{
        width: 80px;
        height: 35px;
        font-size: 14px;
    }
    .nav-cart-count{
        width: 18px;
        height: 18px;
    }
    .nav-login-cart img{
        width:30px
    }
}

@media(max-width:800px){
    .navbar{
        position:fixed;
        z-index:999;
        top:0;
        right:0;
        left:0;
        height: 60px;       
        background:#9c7a7a;
        padding-left:0;
        padding-right: 0;
    }
    .overlayy {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black */
        z-index: -1; /* Ensure the overlay appears on top of other content */
      }
    .nav-dropdown{
        display: block;
        width:30px;
        transition: 0.5s;
        cursor: pointer;
        margin-left: -20px;
    }
    .nav-menu{
        display: none;
        width: 30%;
        position: absolute;
        background-color: #fff;
        justify-content: center;
        top:75px;
    }

    .nav-menu-visible{
        display: grid;
        width:30%;
        height:200px;
        justify-content: center;
        animation: slideIn 0.5s ease-in-out forwards;  
        padding: 10px 0px;  
        border-radius: 10px;
    }
    .nav-menu li{
        height:30px;
        display: grid;
        align-items: center;
    }

    .nav-menu li a{
        color: black;
    }

    @keyframes slideIn {
        from {
          transform: translateY(-20%);
          opacity: 0;
        }
        to {
          transform: translateY(0);
          opacity: 1;
        }
      }
      
      @keyframes slideOut {
        from {
          transform: translateY(0);
          opacity: 1;
        }
        to {
          transform: translateY(-100%);
          opacity: 0;
        }
      }

    ul{
        padding:0;
    }

    .logo img{
        transform: scale(0.7);
        margin-left:-20px;
    }
    .logo{
        margin-left:-20px;
        margin-right: 20px;
    }

}

@media(max-width:500px){
    .navbar{
        width: 8px 0px;
        gap: 0;
    }
    .logo{
        transform: scale(0.8);
    }
    .nav-menu{    
        top: 75px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    }
}